<template>
  <div class="main-page">
    <h1 style="font-size: 2.0rem;">Notes / Records</h1>

    <!-- ÊêúÂ∞ãÂçÄ -->
    <SearchBar v-model="searchQuery" />

    <!-- È°åÁõÆÂàóË°® -->
    <ProblemList :problems="filteredProblems" @selectProblem="selectProblem" />

    <!-- ÂãïÊÖãËºâÂÖ•È°åÁõÆË©≥ÊÉÖ -->
    <transition name="popup" mode="out-in">
      <template v-if="isLoading">
        <LoadingSpinner />
      </template>
      <template v-else>
        <component
          :is="selectedProblemComponent"
          v-if="selectedProblemComponent"
          @close="closeDetail"
        />
      </template>
    </transition>
  </div>
</template>

<script lang="ts">
import { defineComponent, ref, computed } from 'vue';
import { defineAsyncComponent } from 'vue';

// ÂÆöÁæ© Problem ‰ªãÈù¢
interface Problem {
  id: number;
  title: string;
  difficulty: 'easy' | 'medium' | 'hard' | 'other';
  component?: any;
}

// ÂãïÊÖãËºâÂÖ•È°åÁõÆÂÖÉ‰ª∂
const TwoSum = defineAsyncComponent(() => import('./problems/TwoSum.vue'));
const FibonacciSequences = defineAsyncComponent(() => import('./problems/FibonacciSequences.vue'));
const Euclidean = defineAsyncComponent(() => import('./problems/EuclideanAlgorithm.vue'));

// ÂÖ∂‰ªñÂÖÉ‰ª∂
import SearchBar from './SearchBar.vue';
import ProblemList from './ProblemList.vue';
import LoadingSpinner from './LoadingSpinner.vue';

export default defineComponent({
  name: 'MainPage',
  components: { SearchBar, ProblemList, LoadingSpinner },
  setup() {
    // È°åÁõÆÊ∏ÖÂñÆ
    const problems = ref<Problem[]>([
      { id: 1, title: 'Euclidean Algorithm', difficulty: 'other', component:  Euclidean},
      { id: 2, title: 'Fibonacci Sequences', difficulty: 'other', component: FibonacciSequences },
      { id: 3, title: '1_Two Sum', difficulty: 'easy', component: TwoSum },
    ]);

    // ÊêúÂ∞ãÈóúÈçµÂ≠ó
    const searchQuery = ref('');

    // ÈÅ∏‰∏≠ÁöÑÈ°åÁõÆ
    const selectedProblem = ref<Problem | null>(null);
    const selectedProblemComponent = ref<any>(null);

    // ÊéßÂà∂ Loading ÁãÄÊÖã
    const isLoading = ref(false);

    // ÈªûÊìäÊüêÂÄãÈ°åÁõÆÊôÇ
    const selectProblem = (problem: Problem) => {
      isLoading.value = true;
      selectedProblem.value = problem;

      // Ê®°Êì¨ËºâÂÖ•ÂÖÉ‰ª∂ÁöÑÂª∂ÈÅ≤
      setTimeout(() => {
        selectedProblemComponent.value = problem.component;
        isLoading.value = false;
      }, 1000);
    };

    // ÈóúÈñâË©≥ÊÉÖ
    const closeDetail = () => {
      selectedProblem.value = null;
      selectedProblemComponent.value = null;
    };

    return {
      searchQuery,
      filteredProblems: computed(() =>
        problems.value.filter((problem) =>
          problem.title.toLowerCase().includes(searchQuery.value.toLowerCase())
        )
      ),
      selectedProblem,
      selectedProblemComponent,
      isLoading,
      selectProblem,
      closeDetail,
    };
  },
});
</script>

<style scoped>
.main-page {
  padding: 20px;
  height: 70vh;
  width: 90vw;
  font-family: Arial, sans-serif;
  background-color: gray;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  margin: 0 auto;
}

/* Ê®ôÈ°åÂ≠óÈ´îÂ§ßÂ∞è */
h1 {
  font-size: 2rem;
  text-align: center;
}

/* ÈÅéÊ∏°ÊïàÊûúÔºöÁî±‰∏≠ÈñìÂêëÂ§ñÂΩàÈñã */
.popup-enter-active,
.popup-leave-active {
  transition: all 0.3s ease-out;
}

.popup-enter-from,
.popup-leave-to {
  transform: scale(0);
  opacity: 0;
}

.popup-enter-to,
.popup-leave-from {
  transform: scale(1);
  opacity: 1;
}

/* ÊâãÊ©üÁâàÁöÑÂÑ™Âåñ */
@media (max-width: 768px) {
  .main-page {
    padding: 20px;
    width: 87vw;
    border-radius: 20px;
  }

  h1 {
    font-size: 1.5rem;
  }
}

/* Êõ¥Â∞èÁöÑËû¢ÂπïÔºå‰æãÂ¶ÇÊâãÊ©üÊ©´ÊîæÊàñÊõ¥Â∞èÁöÑË£ùÁΩÆ */
@media (max-width: 480px) {
  .main-page {
    padding: 15px;
    width: 87vw;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.15);
    border-radius: 37px;
  }

  h1 {
    font-size: 1.25rem;
  }
}
</style>
